header {
	.container-fluid {
		background-color: $var_pink; } }

.page-header {
	margin: 0 auto;
	min-margin {}
	padding: 0 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	max-width: 960px;
	height: 78px;
	background-color: #fdfdfd;
	@include r(992) {
		padding: 0 25px; } }

.logo {
	padding-top: 3px;
	font-family: "BebasBold", sans-serif;
	font-size: 46px;
	font-weight: bold;
	color: $var_pink;
	text-transform: uppercase;
	line-height: 78px;
	letter-spacing: 0.04em;
	strong {
		color: $var_red; }
	&--inline {
		display: inline-block; } }


.page-name {
	margin: 0 auto;
	padding: 0 10px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	height: 60px;
	max-width: 960px;
	background-color: white;
	h2 {
		padding-top: 1px;
		padding-left: 18px;
		width: 100%;
		font-family: "BebasBold", sans-serif;
		font-size: 30px;
		font-weight: bold;
		color: white;
		background-color: $var_red;
		text-align: left;
		text-transform: uppercase;
		line-height: 60px;
		letter-spacing: 0.03em;
		vertical-align: middle; } }

/*RESPONSIVE NAVBAR*/
.nav {
	padding-bottom: 30px;
	&__main {
		padding: 0;
		ul {
			list-style-type: none;
			margin: 0;
			padding: 0;
			text-align: center; }
		li {
			display: inline-block;
			&:last-child {
				border-right: none; } }
		.current a {
			color: #e8603c; }
		a {
			display: block;
			padding: 5px 0px 0px 22px;
			font-family: "BebasBold", sans-serif;
			font-size: 20px;
			font-weight: bold;
			color: #b9b9b9;
			text-decoration: none;
			text-transform: uppercase;
			line-height: 78px;
			vertical-align: middle;
			transition: 0.25s;
			letter-spacing: 0.04em;
			&:hover {
				color: #e8603c;
				text-decoration: underline; } }
		@include r(720) {
			display: none !important; } }

	&__trigger {
		display: none;
		text-align: center;
		span {
			display: inline-block;
			padding: 0px 30px;
			font-family: "BebasBold", sans-serif;
			font-size: 24px;
			font-weight: bold;
			color: #e8603c;
			text-transform: uppercase;
			line-height: 77px;
			vertical-align: middle;
			cursor: pointer;
			text-transform: uppercase;
			transition: 0.5s;
			&:after {
				display: inline-block;
				margin-left: 10px;
				width: 20px;
				height: 10px;
				font-family: "FontAwesome";
				content: "\f0c9"; }
			&:hover {
				background-color: #e6002d; } }
		.open {
			margin-top: -2px;
			background-color: #e6002d;
			padding: 0px 10px 0px 80px;
			transition: 0.5s; }
		@include r(720) {
			display: block !important; } }

	&__mobile {
		position: relative;
		display: none;
		z-index: 99;
		ul {
			display: none;
			list-style-type: none;
			position: absolute;
			left: 0;
			right: 0;
			margin-left: auto;
			margin-right: auto;
			text-align: center;
			background-color: #e6002d; }
		li {
			display: block;
			padding: 5px 0;
			margin: 0 5px;
			border-bottom: solid 1px #cc0028;
			&:last-child {
				border-bottom: none; } }
		a {
			display: block;
			padding: 10px 30px;
			color: #b9b9b9;
			font-family: "BebasBold", sans-serif;
			font-size: 20px;
			font-weight: bold;
			color: #b9b9b9;
			text-decoration: none;
			text-transform: uppercase;
			&:hover {
				background-color: #e6002d;
				color: #fff; } }
		@include r(720) {
			display: block !important; } } }

.page-content {
	margin: 0 auto;
	max-width: 960px;
	padding: 24px 10px 50px 10px;
	display: grid;
	grid-template-columns: 9fr 3fr;
	@include r(960) {
		display: block;
		grid-template-columns: none;
		padding-bottom: 0;
		max-width: 100%; } }

// .separator
// 	padding: 50px 0
// 	height: 14px
// 	background: url(../img/sep-line.png) repeat-x;
