.post {
	padding-bottom: 47px;
	margin-bottom: 56px;
	position: relative;
	border: solid;
	border-right: 0;
	border-left: 0;
	border-top: 0;
	border-bottom-width: 14px;
	border-image: url(../img/sep-line.png) 0 0 14 0 repeat {
		&:last-of-type {
			border: none;
			padding-bottom: 0; } }
	@include r(1024) {
		margin: 0 auto 50px auto;
		max-width: 670px; }
	&__icon {
		position: absolute;
		left: -85px;
		@include r(1200) {
			display: none; } }
	&__media {
		position: relative;
		max-width: 670px; }
	&__meta {
		padding-top: 20px;
		float: left;
		width: 147px;
		@include r(640) {
			float: none;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			width: 100%; } }
	&__summary {
		padding-top: 20px;
		overflow: hidden; } }

.slider {
	&__arrow-prev {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		left: 18px; }
	&__arrow-next {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 18px; } }


.slider-item {
	img {
		width: 100%; } }

.data {
	padding: 2px 0 0 0;
	margin: 0 0 8px 0;
	@include r(640) {
		text-align: center; }
	&__label {
		padding: 0;
		margin: 0;
		font-family: "BebasBold", sans-serif;
		font-size: 16px;
		font-weight: bold;
		color: $var_red;
		text-transform: uppercase;
		text-decoration: none;
		&:hover {
			text-decoration: underline; } }
	&__value {
		padding-top: 1px;
		font-family: "HelveticaNeueRoman", sans-serif;
		font-size: 12px;
		color: #bababa;
		@include r(640) {
			text-align: center; } } }

.headline {
	a {
		font-family: "BebasBold", sans-serif;
		font-size: 22px;
		color: $var_red;
		text-align: left;
		text-transform: uppercase;
		text-decoration: none;
		letter-spacing: 0.035em;
		&:hover {
			text-decoration: underline; } } }

.textual {
	padding-top: 12px;
	p {
		max-width: 505px;
		font-family: "HelveticaNeueRoman", sans-serif;
		font-size: 13px;
		font-weight: 400;
		font-style: normal;
		color: #414141;
		text-align: left;
		line-height: 1.4em;
		padding-bottom: 18px;
		&:last-of-type {
			padding-bottom: 0px; } }
	&__url {
		padding-top: 5px;
		padding-bottom: 20px;
		font-family: "HelveticaNeueRoman", sans-serif;
		font-size: 14px;
		font-weight: 400;
		font-style: normal;
		color: #a0a0a0; }
	&--sidebar-text {
		p {
			padding: 0;
			color: #464646; } } }

.quote {
	padding: 20px 50px 20px 50px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	background-color: #f5f5f5;
	@include r(640) {
		display: flex;
		flex-direction: column;
		justify-content: flex-start;
		padding: 20px; }
	&__mark {
		padding-left: 7px;
		padding-right: 20px;
		font-family: Georgia, sans-serif;
		font-size: 72px;
		color: $var_red;
		line-height: 1em; }
	&__text {
		font-family: "HelveticaNeueItalic", sans-serif;
		font-size: 14px;
		font-weight: 300;
		font-style: italic;
		color: #414141;
		text-align: left;
		line-height: 1.5em; } }


.pagination-list {
	margin-top: 50px;
	li {
		display: inline-block;
		line-height: 26px;
		background-color: $var_pink;
		vertical-align: middle;
		transition: 0.2s;
		&:hover {
			background-color: $var_red; } }
	a {
		padding: 0 9px;
		text-decoration: none;
		font-family: "BebasRegular", sans-serif;
		font-size: 14px;
		font-weight: normal;
		font-style: normal;
		color: #fff;
		line-height: 26px; }
	.current {
		background-color: $var_red; }
	@include r(960) {
		display: flex;
		flex-direction: row;
		justify-content: center;
		padding-bottom: 50px;
		li {
			margin: 0 5px; } } }

.sidebar {
	padding: 0 5px;
	@include r(960) {
		max-width: 670px;
		margin: 0 auto; } }

.sidebar-section {
	padding-bottom: 30px;
	&__header {
		display: flex;
		flex-direction: row;
		background: url(../img/sep-line.png);
		height: 18px; }
	.h4 {
		padding-right: 10px;
		background-color: white;
		font-family: "BebasBold", sans-serif;
		font-size: 20px;
		font-weight: 700;
		font-style: bold;
		color: $var_red;
		text-transform: uppercase;
		text-decoration: none;
		&:hover {
			text-decoration: underline; } }
	p, a {
		font-family: "HelveticaNeueRoman", sans-serif;
		font-size: 13px;
		font-weight: 400;
		font-style: normal;
		color: #464646;
		line-height: 1.25em; }
	a {
		text-decoration: none;
		&:hover {
			text-decoration: underline; } }
	@include r(960) {
		li {
			text-align: center; } } }

.gallery {
	padding-top: 20px;
	@include r(960) {
		text-align: center; }
	&__pic-frame {
		display: inline-block;
		background-color: $var_pink;
		border: 1px solid $var_red; }
	&--small {
		.gallery__pic-frame {
			margin: 3px;
			height: 48px;
			width: 48px; }
		img {
			width: 46px;
			height: 46px; } } }

footer {
	.container-fluid {
		background-color: $var_pink; }

	.container {
		margin: 0 auto;
		padding: 0 10px;
		max-width: 960px;
		background-color: #fdfdfd; } }

.info {
	display: flex;
	flex-direction: row;
	padding: 35px;
	background-color: $var_red;
	@include r(480) {
		flex-direction: column; }
	&__about {
		max-width: 260px; }
	h4 {
		padding-bottom: 25px;
		color: #fff;
		font-family: "BebasBold", sans-serif;
		font-size: 24px;
		font-weight: 700;
		font-style: bold; }
	p {
		padding-bottom: 15px;
		font-family: "HelveticaNeueRoman", sans-serif;
		font-size: 13px;
		font-weight: 400;
		font-style: normal;
		color: #fff;
		text-align: left;
		line-height: 1.25em; }
	&__twitter-link {
		color: $var_pink;
		cursor: pointer;
		text-decoration: none;
		&:hover {
			text-decoration: underline; } }
	&__twitter {
		padding-left: 60px;
		max-width: 212px; } }
